import React from 'react';
import { Jumbotron } from 'react-bootstrap';
import '../styles/HomeInfo.css';

const HomeInfo = () => {
    return (
        <div id="zalety">
            <Jumbotron className="jumbotron">
            <h1>Dlaczego warto si zarejestrowa w naszej aplikacji?</h1>
            <br></br>
            <ul>
                <li>Zapisz swoje preferencje oraz adres aby uzyskiwa spersonalizowane oferty orodkw</li>
                <li>Dodawaj orodki do ulubionych, aby z atwoci sprawdza aktualne warunki pogodowe</li>
                <li>Oceniaj orodki i pom w tworzeniu rakingu najlepszych orodkw w centralnej Europie</li>
            </ul>
            </Jumbotron>
        </div>
    )
}

export default HomeInfo;